.labelWrap {
  margin-top: 15px;
}
.labelWrap .labelTitle {
  display: inline-block;
  width: 80px;
  text-align: right;
}
.labelWrap .labelCont {
  display: inline-block;
  width: 570px;
  padding: 0 8px;
}
.labelWrap .colorRed {
  color: red;
}
.labelWrap .errorTips {
  margin-left: 88px;
  color: red;
  line-break: 30px;
}
.labelWrap .labelChildWrap {
  margin-bottom: 15px;
  margin-top: 15px;
  margin-left: 88px;
}
.labelWrap .labelChildWrap > label.labelChildTitle {
  display: inline-block;
  vertical-align: middle;
}
.labelWrap .labelChildWrap > p.labelChildCont {
  display: inline-block;
  vertical-align: middle;
  padding-left: 3px;
}
.labelWrap .labelChildWrap .errorTips {
  margin-left: 40px;
}
